import React, { useState } from "react";
import Modal from "./../../components/Modal/index";

import { makeStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import Checkbox from "@material-ui/core/Checkbox";
import FormControl from "@material-ui/core/FormControl";
import Button from "../../components/Button";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";

import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import "./index.css";
import { Link } from "react-router-dom";

const CreateNewGroup = () => {
  const useStyles = makeStyles((theme) => ({
    formControl: {
      display: "block",
      margin: theme.spacing(1),
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
  }));

  const classes = useStyles();

  const [group, setGroup] = useState("");
  const [category, setCategory] = useState("");
  const [subscription, setSubscription] = useState("");
  const [recurrence, setRecurrence] = useState("");
  const [customRecurrence, setCustomRecurrence] = useState("");
  const [price, setPrice] = useState("");

  const handleCategoryChange = (event, method) => {
    setCategory(event.target.value);
  };

  const handleSubscriptionChange = (event, method) => {
    setSubscription(event.target.value);
  };

  const handleRecurrenceChange = (event, method) => {
    setRecurrence(event.target.value);
  };

  const handleCustomRecurrenceChange = (event, method) => {
    setCustomRecurrence(event.target.value);
  };

  const handlePriceChange = (event, method) => {
    setPrice(event.target.value);
  };

  return (
    <Modal title="Create New Group" open="show" displayModal={true}>
      <div className="create-group">
        <div className="form-group">
          <label>Name your group</label>
          <input
            type="text"
            className="form-control"
            placeholder="Social Circle"
            value={group}
            onChange={(e) => {
              setGroup(e.target.value);
            }}
          />
        </div>
        <div className="form-group">
          <label>Choose Group Category</label>
          <FormControl fullWidth className={classes.formControl}>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={category}
              onChange={handleCategoryChange}
              fullWidth
            >
              <MenuItem value="General">General</MenuItem>
              <MenuItem value="UI/UX Designing">UI/UX Designing</MenuItem>
              <MenuItem value="Social Media Marketing">
                Social Media Marketing
              </MenuItem>
              <MenuItem value="Social Learning">Social Learning</MenuItem>
              <MenuItem value="Jobs">Jobs</MenuItem>
              <MenuItem value="Learning">Learning</MenuItem>
              <MenuItem value="Freelance ">Freelance </MenuItem>
              <MenuItem value="Work">Work</MenuItem>
            </Select>
          </FormControl>
        </div>
        <form>
          <div className="form-group">
            <label for="exampleFormControlFile1">Add Banner</label>
            <input
              type="file"
              className="form-control-file"
              id="exampleFormControlFile1"
            />
          </div>
        </form>
        <div className="form-group purchase-plan">
          <label>Group Purchased Plan</label>

          <Accordion>
            <AccordionSummary
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <FormControlLabel
                aria-label="Free"
                control={<Checkbox />}
                label="Free"
              />
            </AccordionSummary>
          </Accordion>
          <Accordion>
            <AccordionSummary
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <FormControlLabel
                aria-label="Paid"
                onClick={(event) => event.stopPropagation()}
                onFocus={(event) => event.stopPropagation()}
                control={<Checkbox />}
                label="Paid"
              />
            </AccordionSummary>
            <AccordionDetails>
              <div className="container">
                <div className="row">
                  <div className="col-6">
                    <FormControl fullWidth className={classes.formControl}>
                      <span>Subscription Type</span>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={subscription}
                        onChange={handleSubscriptionChange}
                        fullWidth
                      >
                        <MenuItem value="General">1 time subscription</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                  <div className="col-6">
                    <FormControl fullWidth className={classes.formControl}>
                      <span>Recurrence</span>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={recurrence}
                        onChange={handleRecurrenceChange}
                        fullWidth
                      >
                        <MenuItem value="Weekly">Weekly</MenuItem>
                        <MenuItem value="Monthly">Monthly</MenuItem>
                        <MenuItem value="Yearly">Yearly</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                  <div className="col-8">
                    <span>Create Custom Recurrence</span>
                  </div>
                  <div className="col-4">
                    <FormControl fullWidth className={classes.formControl}>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={customRecurrence}
                        onChange={handleCustomRecurrenceChange}
                        fullWidth
                      >
                        <MenuItem value="Weekly">Weekly</MenuItem>
                        <MenuItem value="Monthly">Monthly</MenuItem>
                        <MenuItem value="Yearly">Yearly</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                  <div className="col-8">
                    <span>Set Price for </span>
                  </div>
                  <div className="col-4">
                    <FormControl fullWidth className={classes.formControl}>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={price}
                        onChange={handlePriceChange}
                        fullWidth
                      >
                        <MenuItem value="Weekly">Weekly</MenuItem>
                        <MenuItem value="Monthly">Monthly</MenuItem>
                        <MenuItem value="Yearly">Yearly</MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                  <div className="col-12">
                    <span>
                      Note: To manage content access for each fan level or
                      create additional fan levels for this group as well as
                      other groups that you manage, goto{" "}
                      <Link>fan global page</Link>
                    </span>
                  </div>
                </div>
              </div>
            </AccordionDetails>
          </Accordion>
        </div>
        <div className="form-group">
          <label>Select Privacy</label>
          <br />
          <div className="input-group">
            <div className="dropdown w-100">
              <button
                className="btn btn-lg btn-secondary dropdown-toggle w-100"
                type="button"
                id="dropdownMenuButton"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <div className="inline-block mr-2">
                  <i className="fa fa-lock"></i>
                </div>
                <div className="inline-block">
                  <span className="d-block">Private</span>
                  <span>
                    (Only members can see who's in the group and what they post)
                  </span>
                </div>
              </button>
              <div
                className="dropdown-menu w-100"
                aria-labelledby="dropdownMenuButton"
              >
                <a className="dropdown-item" href="#">
                  <div className="inline-block mr-2">
                    <i className="fa fa-globe"></i>
                  </div>
                  <div className="inline-block">
                    <span className="d-block">Public</span>
                    <span>
                      (Anyone can see who's in the group and what they post)
                    </span>
                  </div>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className="form-group">
          <label>Hide Group</label>
          <br />
          <div className="input-group">
            <div className="dropdown w-100">
              <button
                className="btn btn-lg btn-secondary dropdown-toggle w-100"
                type="button"
                id="dropdownMenuButton"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <div className="inline-block mr-2">
                  <i className="fa fa-eye"></i>
                </div>
                <div className="inline-block">
                  <span className="d-block">Visible</span>
                  <span>(Anyone can find this group)</span>
                </div>
              </button>
              <div
                className="dropdown-menu w-100"
                aria-labelledby="dropdownMenuButton"
              >
                <a className="dropdown-item" href="#">
                  <div className="inline-block mr-2">
                    <i className="fa fa-eye-slash"></i>
                  </div>
                  <div className="inline-block">
                    <span className="d-block">Hidden</span>
                    <span>(Only members can find this group)</span>
                  </div>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="">
        <Button variant="primary" size="compact m-2 float-right">
          Create
        </Button>
      </div>
    </Modal>
  );
};

export default CreateNewGroup;
